body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: linear-gradient(#E3F0FF);
}

code { 
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*background-image: url('./assets/food.jpg');*/
*{
    margin: 0px;
    padding: 0px;
    
}

.back{
  background-color: linear-gradient(to bottom right, #E3F0FF, #FAFCFF);;
  margin: 0;
  padding: 0px;
}

.contentWrapper{
    display: flex;
    flex: 1 1;
    flex-direction: row;
    flex-wrap:wrap;
    grid-gap: 30px 30px;
    gap: 30px 30px;
    justify-content: center;
    
}

.background{
  display: inline-flex;
  flex: 0.25 1;
  align-items: center;
  justify-content: center;

}

.number{
    width: 20px;
    height: 20px;
    border: 1px solid white;
    border-radius: 50%;
    font-size: 10px;
    padding: 3px;
    text-align: center;
    background-color: linear-gradient(to bottom right, #E3F0FF, #FAFCFF);;
    margin: 5px 5px;
    font-style: Sans-serif;
    color: white
}

.name{
    font-family:  'Brush Script Std', cursive;
    text-align: center;
    text-shadow: 1px  1px white;

}

.image{
    flex: 1 1;
    border-radius: 5px;
    width: 200px;
    height: 200px;
    border: 2px solid black;
    margin: 15px;
}

.price{
    font-family:  'Brush Script Std', cursive;
    text-align: center;
    text-shadow: 1px  1px white;
    font-size: 16px;
    font-weight: 500;
    margin-top: 8px;
}

.cartBtn{
  width: 80px;
  height: 30px;
  display: block;
  justify-items: center;
  align-items: center;
  margin: 20px auto 0px auto;
  border: 2px solid black;
  border-radius: 10px;
  font-family: 'Brush Script Std', cursive;
  font-size: 12px;
  font-weight: 550;
}

.cartBtn:hover{
  background-color: peachpuff;
  text-shadow: white;
}

.navBtn{
  border-radius: 5px;
  background-color: #E3F0FF;
  border-radius: 12px;
  color: black;
  text-align: center;
  font-family: 'Brush Script Std', cursive;
  font-size: 20px;
  width: 125px;
  height: 40px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 2px;
  margin: 10px;
}

.navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
   
} 

.navBtn:hover{
    background-color: peachpuff;
    text-shadow: white;
}

.navBtn span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .navBtn span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .navBtn:hover span {
    padding-right: 25px;
  }
  
  .navBtn:hover span:after {
    opacity: 1;
    right: 0;
  }
.cartBody{
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom right, #E3F0FF, #FAFCFF);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
   }


.Cart-Container{
    width: 70%;
    height: 85%;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0px 25px 40px #1687d933;
   }

.Header{
 margin: auto;
 width: 90%;
 height: 15%;
 display: flex;
 justify-content: space-between;
 align-items: center;
}

.Heading{
 font-size: 20px;
 font-family: 'Open Sans';
 font-weight: 700;
 color: #2F3841;
}

.Action{
 font-size: 14px;
 font-family: 'Open Sans';
 font-weight: 600;
 color: #E44C4C;
 cursor: pointer;
 border-bottom: 1px solid #E44C4C;
}

.Cart-Items{
    margin: auto;
    width: 90%;
    height: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
   }
   .image-box{
    width: 15%;
    text-align: center;
   }
   .about{
    height: 100%;
   }
   .title{
    padding-top: 5px;
    line-height: 10px;
    font-size: 32px;
    font-family: 'Open Sans';
    font-weight: 800;
    color: #202020;
   }
   .subtitle{
    line-height: 10px;
    font-size: 18px;
    font-family: 'Open Sans';
    font-weight: 600;
    color: #909090;
   }

   .counter{
    width: 15%;
    display: flex;
    justify-content: space-between;
    align-items: center;
   }
   .btn{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #d9d9d9;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-family: ‘Open Sans’;
    font-weight: 900;
    color: #202020;
    cursor: pointer;
   }
   .count{
    font-size: 20px;
    font-family: ‘Open Sans’;
    font-weight: 900;
    color: #202020;
   }
   .prices{
    height: 100%;
    text-align: right;
   }
   .amount{
    padding-top: 20px;
    font-size: 26px;
    font-family: ‘Open Sans’;
    font-weight: 800;
    color: #202020;
   }
   .save{
    padding-top: 5px;
    font-size: 14px;
    font-family: ‘Open Sans’;
    font-weight: 600;
    color: #1687d9;
    cursor: pointer;
   }
   .remove{
    padding-top: 5px;
    font-size: 14px;
    font-family: ‘Open Sans’;
    font-weight: 600;
    color: #E44C4C;
    cursor: pointer;
   }

   hr{
    width: 66%;
    float: right;
    margin-right: 5%;
   }
   .checkout{
    float: right;
    margin-right: 5%;
    width: 28%;
   }
   .total{
    width: 100%;
    display: flex;
    justify-content: space-between;
   }
   .Subtotal{
    font-size: 22px;
    font-family: ‘Open Sans’;
    font-weight: 700;
    color: #202020;
   }
   .items{
    font-size: 16px;
    font-family: ‘Open Sans’;
    font-weight: 500;
    color: #909090;
    line-height: 10px;
   }
   .total-amount{
    font-size: 36px;
    font-family: ‘Open Sans’;
    font-weight: 900;
    color: #202020;
   }
   .button{
    margin-top: 5px;
    width: 100%;
    height: 40px;
    border: none;
    background: linear-gradient(to bottom right, #B8D7FF, #8EB7EB);
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    font-family: ‘Open Sans’;
    font-weight: 600;
    color: #202020;
   }


   .cartIcon{
    position: relative;
    left: 10%;
    font-size: 24px;
   }

   .search{
    position: relative;
    left: 5%;
    border: 2px solid;
    border-radius: 8px;
    width: 220px;
    box-shadow: #E44C4C;
   }
